//переменные
$bg-color: #aabbc7;
$main-color: #f0f5f9;
$second-color: #fff;
$border-color: #d3e0ea;
$btn-color: #3498db;

* {
  font-family: 'Jost', sans-serif;
}


html {
  cursor: url("../public/cursor.png"), auto;
  font-family: 'Jost', sans-serif;

  body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    margin: 0;
    background-color: $bg-color;

    //основной блок
    .calculator-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: $main-color;
      padding: 20px;
      border-radius: 8px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      width: 20vw;
      margin: 0 auto;
    }

    //инпуты
    input {
      cursor: url("../public/text_select.png"), text;
    }

    .input-field {
      width: 100%;
      height: 30px;
      margin: 8px;
      padding: 8px;
      border: 1px solid $border-color;
      border-radius: 4px;
      outline: none;
    }

    input[type="number"] {
      -moz-appearance: textfield;
      appearance: textfield;
    }

    input[type="number"]::-webkit-inner-spin-button,
    input[type="number"]::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    .result-field {
      width: 100%;
      height: 30px;
      margin-top: 16px;
      padding: 8px;
      border: 1px solid $border-color;
      border-radius: 4px;
      font-size: 18px;
      text-align: center;
      background-color: $second-color;
      outline: none;
    }

    // кнопка операнд
    .operator-button {
      width: 50px;
      height: 40px;
      margin: 8px;
      font-size: 20px;
      border: none;
      border-radius: 10%;
      background-color: $btn-color;
      color: $second-color;
      cursor: url("../public/pointer.png"), pointer;
      transition: background-color 0.3s ease;
      font-weight: 600;

      &:hover {
        background-color: darken($btn-color, 10%);
      }
    }

    // модальное окно и блюр
    .dialog {
      background-color: $second-color;
      padding: 20px;
      border-radius: 8px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      border: none;

      p {
        margin-bottom: 16px;
      }

      button {
        display: block;
        margin: 0 auto;
        padding: 8px 16px;
        background-color: $btn-color;
        color: $second-color;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: darken($btn-color, 10%);
        }
      }
    }

    .backdrop {
      height: 100vh;
      width: 100vw;
      position: fixed;
      top: 0;
      left: 0;
      backdrop-filter: blur(8px);
    }
  }
}